@charset "UTF-8";
/** 縦書き対応 */
.vertical-writing {
  writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  height: 100px;
  word-wrap: break-word;
  vertical-align: middle;
  text-align: center;
  display: inline; }

.col-vertical-writing {
  vertical-align: middle;
  text-align: center; }

.parsley-errors-list {
  display: none; }

.tooltipster-base {
  pointer-events: auto; }

.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-border,
.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-background {
  border-top-color: #000; }

.tooltipster-sidetip .tooltipster-box {
  background: #000;
  border: none;
  border-radius: 0; }

.tooltip-wrapper .tooltip-close {
  display: none; }

.tooltipster-sidetip .tooltipster-content {
  padding: 5px 14px 6px 14px; }

.question strong, .finisher strong {
  font-style: inherit;
  font-weight: bold; }

.question em, .finisher em {
  font-family: 'Times New Roman','ヒラギノ明朝 Pro W6', 'Hiragino Mincho Pro', 'ＭＳ Ｐゴシック', serif;
  font-style: italic;
  font-weight: inherit; }

.question .notice, .finisher .notice {
  font-weight: normal; }

.formErrorMsg.formErrorContent {
  color: #fff;
  padding: 0.5em;
  box-sizing: border-box; }

.question-form-list input.number,
.MultiNumberQuestion input.number {
