    .matrix.question-form-grid-table td label > span {
      margin: 0 5px 0 0; }

.question-description table {
  border-collapse: collapse; }
  .question-description table td, .question-description table th {
    padding: 1px 3px;
    border: 1px solid #888; }

.page-no {
  position: absolute;
  right: 0;
  top: 0;
  background: #f9f3e0;
  border-left: 1px solid gold;
  border-bottom: 1px solid gold;
  border-radius: 0 4px 0 4px;
  padding: 0 10px;
  z-index: 1; }

.branch-no {
  position: absolute;
  right: 0;
  top: 0;
  background: #e7e1f9;
  border-left: 1px solid purple;
  border-bottom: 1px solid purple;
  border-radius: 0 4px 0 4px;
  padding: 0 10px;
  z-index: 1; }

.finisher-no {
  position: absolute;
  right: 0;
  top: 0;
  background: #e6f6d7;
  border-left: 1px solid green;
  border-bottom: 1px solid green;
  border-radius: 0 4px 0 4px;
  padding: 0 10px;
  z-index: 1; }

.hidden {
  display: none; }

/* フォーム　エラー表示　SP */
