.m3-enquete__user-agent-group--SP .questionBox .question .matrix td {
  padding: 0.6em 0; }
  .m3-enquete__user-agent-group--SP .questionBox .question .matrix td label {
    padding: 0; }

.m3-enquete__user-agent-group--SP .questionBox .question .formErrorContent {
  width: calc(100% - 8px); }

.m3-enquete__user-agent-group--SP .questionBox .question img {
